import { z } from 'zod';
export var loginSchema = z.object({
    email: z.string().min(1, ' email').email(' email'),
    password: z.string().min(6, ' 6 ')
});
export var registerSchema = z.object({
    name: z.string().min(1, ' '),
    surname: z.string().min(1, ' '),
    email: z.string().min(1, ' email').email(' email'),
    password: z.string().min(6, ' 6 '),
    passwordRepeat: z.string().min(1, ' ')
}).refine(function (data) {
    return data.password === data.passwordRepeat;
}, {
    message: '  ',
    path: ['passwordRepeat']
});
